.spinner-wrapp{
    height: 80vh;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.app{
    position: relative;
    height: 100vh;
}
.content{
    margin-top: 30px;
    font-family: sans-serif;
    display: -ms-flexbox;
    display: flex;
    width: auto;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
