.footer-wrapp{
    position: absolute;
    bottom: 0;
    left: 0;
}
footer{
    height: 60px;
    width: 100vw;
    background: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.socials{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.socials a{
    display: block;
}
.socials img{
    margin: 0 10px;
    height: 36px;
    -webkit-transition: .2s ease-in-out;
    -o-transition: .2s ease-in-out;
    transition: .2s ease-in-out;
}
 
.socials a:hover img{
-webkit-transform: scale(1.05);
    -ms-transform: scale(1.05);
        transform: scale(1.05);
}


